import React, { Component } from "react"
import { Button, Form, Input } from "antd"

import BraftEditor from "braft-editor"
import "braft-editor/dist/index.css"
import api from "../../../api"

type StateType = {
  editorState: string;
  title: string;
  tag: string;
};
type propType = {
  userInfo:any
};

class NewQuestion extends Component<propType, StateType> {
  state = {
    // editorState
    editorState: BraftEditor.createEditorState(null),
    title:"",
    tag:""
  }

  async componentDidMount () {
    // html
    // BraftEditor.createEditorStatehtmleditorStat
    this.setState({
        editorState: BraftEditor.createEditorState("")
    })
  }

  submitContent = async () => {
    // ctrl+s
    // editorState.toHTML()HTML
    const htmlContent = this.state.editorState.toHTML()
    console.log(htmlContent)
    // const result = await saveEditorContent(htmlContent)
  }

  handleEditorChange = (editorState: any) => {
    this.setState({ ...this.state, editorState })
  }

  handleTitleChange = (e: any) => {
    this.setState({
      ...this.state,
      title: e.target.value
    })
  }

  handleTagChange = (e: any) => {
    this.setState({
      ...this.state,
      tag: e.target.value
    })
  }

  handleConfirm = () => {
    api.newQuestion({
      authorId: this.props.userInfo.id,
      mainPic:"/api/static/media/1.b4b91e14.jpg",
      content: this.state.editorState.toHTML(),
      title:this.state.title,
      tag:this.state.tag
    }).then(res => {
      console.log(res)
    }).catch(err => {
      console.log(err)
    })
  }

  render () {
    const { editorState } = this.state
    return (
      <Form
        name="basic"
        labelCol={{ span: 3 }}
        wrapperCol={{ span: 18 }}
        onFinish={this.handleConfirm}
        onFinishFailed={(err) => { console.log(err) }}
      >
        <Form.Item
          label=""
          name="title"
          rules={[{ required: true, message: "Please input your username!" }]}
        >
          <Input onChange={this.handleTitleChange} />
        </Form.Item>

        <Form.Item
          label=""
          name="tag"
          rules={[{ required: true, message: "Please input your password!" }]}
        >
          <Input onChange={this.handleTagChange} />
        </Form.Item>

        <Form.Item
          label=""
          name="content"
          rules={[{ required: true, message: "Please input your password!" }]}
        >
          <BraftEditor
            value={editorState}
            onChange={this.handleEditorChange}
            onSave={this.submitContent}
          />
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Form.Item>
      </Form>
      )
  }
}

export default NewQuestion
