import React from "react"
import { useSelector } from "react-redux"
import { Link, useNavigate } from "react-router-dom"

const UserCenterMore = () => {
    const { username } = useSelector((state: any) => state.user.userInfo)
    const nav = useNavigate()
    function handleLogout() {
        localStorage.removeItem("userInfo")
        localStorage.removeItem("token")
        nav("/login", { replace:true })
    }
    return(
        <div className="user-center-more user-center-more-hidden">
            <span className="user-center-more-arrow"></span>
            <div className="user-center-section">
                <Link className="user-center-li" to={"/people/" + username}></Link>
                <Link className="user-center-li" to="/creator"></Link>
                <Link className="user-center-li" to="/settings"></Link>
                <div className="user-center-li" onClick={handleLogout}></div>
            </div>
        </div>
    )
}

export default UserCenterMore
