import { message } from "antd"
import moment from "moment"
import React, { useEffect, useState } from "react"
import { useSelector } from "react-redux"
import { useParams } from "react-router-dom"
import api from "../../api"
import PeopleDynamicUnit from "../../components/People/PeopleDynamicUnit/PeopleDynamicUnit"

const titleData = {
  1: "",
  2: "",
  3: "",
  4: ""
}

type titleType = keyof typeof titleData

function PeopleIndex() {
  const { username } = useParams() || useSelector((state: any) => state.user.userInfo)
  const [dynamics,setDynamics] = useState([])
  useEffect(() => {
    if (username) {
      api.getUserDynamicByUsername(username).then((res: any) => {
        setDynamics(res.data.data)
      }).catch((err: any) => {
        message.error(err.state)
      })
    }
  },[username])
  return (
    <div className="people-index">
      <div className="people-center-header"></div>
      {dynamics?.map((dynamic: {type:number,question:any,createTime:string}, key) => {
        return (
          <div className="people-index-item" key={key}>
            <div className="index-item-header">
              <div>{titleData[dynamic.type as unknown as titleType]}</div>
              <div>{moment(dynamic.createTime).format("yyyy-MM-DD HH:mm:ss")}</div>
            </div>
            <div className="index-item-body">
              <div className="item-body-title">{dynamic.question.title}</div>
              {dynamic.question.answer && <PeopleDynamicUnit dynamic={dynamic} />}
            </div>
          </div>
        )
      })}
    </div>
  )
}

export default PeopleIndex
