import React from "react"
import { Outlet, Link } from "react-router-dom"
import "./style.less"

const Main = () => {
    return (
        <div className="bd-l">
            <div className="bd-header">
                <ul>
                    <Link className="bd-header-li" to="/follow"></Link>
                    <Link className="bd-header-li" to="/"></Link>
                    <Link className="bd-header-li" to="/hot"></Link>
                    <Link className="bd-header-li" to="/video"></Link>
                </ul>
            </div>
            <div className="bd-content">
                <Outlet />
            </div>
        </div>
    )
}

export default Main
